.tableList{
  margin-top: 20px;  
    overflow-x: hidden;
    overflow-y: scroll;
    height: 400px;
    color: #6a5d5d;

}
.tableList tr{
    display: flex;
    justify-content: space-between;
}
.tableList td{
    padding: 0.5rem;
}
.tableList tr:nth-of-type(odd){
    background-color: #f3f2f8;
}
